body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
}

.section-heading{
  font-family: 'Do Hyeon', sans-serif;
  color: #078DD2;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.266);
}

.color-primary {
  color: #078DD2;
}

.color-secondary{
  color: #99d9f6;
}

.color-highlight{
  color: orange;
}

.ls-2{
  letter-spacing: 3px;
}

.btn-custom{
  background: #078DD2 !important;
  color: #fff !important;
}
.site-footer {
  background-color: #002E60;
  padding: 45px 0 20px;
  font-size: 15px;
  line-height: 24px;
  color: #979696;
}

.site-footer hr {
  border-top-color: #bbb;
  opacity: 0.5;
}

.site-footer hr.small {
  margin: 20px 0;
}

.site-footer h6 {
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
  margin-top: 5px;
  letter-spacing: 2px;
}

.site-footer a {
  color: #737373;
}

.site-footer a:hover {
  color: #3366cc;
  text-decoration: none;
}

.footer-links {
  padding-left: 0;
  list-style: none;
}

.footer-links li {
  display: block;
}

.footer-links a {
  color: #737373;
}

.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
  color: #3366cc;
  text-decoration: none;
}

.footer-links.inline li {
  display: inline-block;
}

.site-footer .social-icons {
  text-align: right;
}

.site-footer .social-icons a {
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin-left: 6px;
  margin-right: 0;
  border-radius: 100%;
  background-color: #33353d;
}

.copyright-text {
  margin: 0;
}

@media (max-width: 991px) {
  .site-footer [class^="col-"] {
    margin-bottom: 30px;
  }
}

@media (max-width: 767px) {
  .site-footer {
    padding-bottom: 0;
  }

  .site-footer .copyright-text,
  .site-footer .social-icons {
    text-align: center;
  }
}

.social-icons {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.social-icons li {
  display: inline-block;
  margin-bottom: 4px;
}

.social-icons li.title {
  margin-right: 15px;
  text-transform: uppercase;
  color: #96a2b2;
  font-weight: 700;
  font-size: 13px;
}

.social-icons a {
  background-color: #eceeef;
  color: #818a91;
  font-size: 16px;
  display: inline-block;
  line-height: 44px;
  width: 44px;
  height: 44px;
  text-align: center;
  margin-right: 8px;
  border-radius: 100%;
  transition: all 0.2s linear;
}

.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
  color: #fff;
  background-color: #29aafe;
}

.social-icons.size-sm a {
  line-height: 34px;
  height: 34px;
  width: 34px;
  font-size: 14px;
}

.social-icons a.facebook:hover {
  background-color: #3b5998;
}

.social-icons a.twitter:hover {
  background-color: #00aced;
}

.social-icons a.linkedin:hover {
  background-color: #007bb6;
}

.social-icons a.dribbble:hover {
  background-color: #ea4c89;
}

@media (max-width: 767px) {
  .social-icons li.title {
    display: block;
    margin-right: 0;
    font-weight: 600;
  }
}

.profileImage {
  width: 400px;
}

.styleOfHeader {
  /* border: 1px solid black; */
  /* padding: 0px 100px; */
  background: linear-gradient(to bottom, #f6f6f6 50%, #e4e5e0 50%);
  height: 100vh;
  padding: 25px 50px;
}

.headerProfileImage {
  height: 95vh;
  width: auto;
  margin-bottom: 0;
}

@media only screen and (max-width: 425px) {
  /*Small smartphones [325px -> 425px]*/

  .styleOfHeader {
    /* border: 1px solid black; */
    /* padding: 0px 100px; */
    background: linear-gradient(to bottom, #f6f6f6 50%, #e4e5e0 50%);
    height: auto;
    padding: 20px 30px;
  }
}

.my-name {
  font-family: "Russo One", sans-serif;
  font-size: 60px;
}


.contact {
  background: linear-gradient(to right, 
    #F6F6F6 50%,
	#E4E5E0 50% 
    );
  overflow: hidden;
  padding: 50px 0px;
  display: -webkit-flex;
  display: flex;
}

.contactStyle {
  border: 1px solid red;
}

.contact .from {
}

.contact .from label {
  display: block;
  margin: 10px 0;
}

.contact .from input,
.contact .from textarea{
  background-color: #fff;
  padding: 10px;
  border: none;
  border-radius: 0 !important;
  margin-bottom: 5px;
}

