.flipBook {
  position: relative;
}

.flipBook:before,
.flipBook:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 10px;
  width: 50%;
  top: 80%;
  max-width: 300px;
  background: #777;
  box-shadow: 0 15px 10px #777;
  -webkit-transform: rotate(-3deg);
  transform: rotate(-3deg);
}

.flipBook:after {
  -webkit-transform: rotate(3deg);
  transform: rotate(3deg);
  right: 10px;
  left: auto;
}

.flip-book-img-container {
  position: relative;
}

.flip-book-img-container img {
  width: 100%;
  height: 100%;
}

.flip-book-overlay {
  position: absolute;
  left: 0;
  bottom: 25;
  background: rgba(0, 0, 0, 0.7); /* Black see-through */
  color: #f1f1f1;
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
  text-decoration: none;
}

.flip-book-img-container:hover .flip-book-overlay {
  opacity: 1;
  color: #fff;
  text-decoration: none;
}

.flip-book-text h2 {
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.255);
  font-family: "Pacifico", cursive;
  line-height: 60px;
  font-size: 45px;
  font-weight: bolder;
  color: #2c3a47;
}

